app.run(['$rootScope', '$state', 'Session', 'toastr', 'baSidebarService', function ($rootScope, $state, Session, toastr, baSidebarService) {
    Session.init();
    $(document).ready(function () {
        baSidebarService.setMenuCollapsed(true);

        $rootScope.$on("$stateChangeStart", function (event, toState, toParams, fromState, fromParams) {
            if (!('data' in toState) || !('access' in toState.data)) {
                toastr.warning("Access undefined for this state", '<strong>Undefined State Access</strong>');
                event.preventDefault();
            }
            else if (!Session.authorize(toState.data.access)) {
                toastr.error("Seems like you tried accessing a route you don't have access to!", '<strong>Forbidden!</strong>');
                event.preventDefault();

                /*TODO: NA DOYME TI KANEI AYTO vvv*/
                if (fromState.url === '^') {
                    console.log("fromState.url === '^' = true");
                    if (Session.isActive()) {
                        console.log("Session.isActive() = true");
                        toastr.success("Session.isActive() = true" + $rootScope.error, '<strong>STATE CHANGE START</strong>');
                        //$state.go('news');
                    } else {
                        console.log("Session.isActive() = false");
                        $rootScope.error = null;
                        toastr.warning("Session.isActive() = false" + $rootScope.error, '<strong>STATE CHANGE START</strong>');
                        //$state.go('news');
                    }
                }
            }
        });

        $rootScope.$on('$stateChangeError', function (event, toState, toParams, fromState, fromParams, error) {
            event.preventDefault();
            $rootScope.$pageFinishedLoading = true;
        });

        $rootScope.$on('$stateChangeSuccess', function (event, toState, toParams, fromState, fromParams, error) {
            $rootScope.$pageFinishedLoading = true;
        });

        console.log("The Application Loading Was Completed!");
    });

}]);

(function(exports){

    var config = {

        /* List all the roles you wish to use in the app
        * You have a max of 31 before the bit shift pushes the accompanying integer out of
        * the memory footprint for an integer
        */
        roles :[
            'public',
            'user',
            'host',
            'renter',
            'admin'],

        /*
        Build out all the access levels you want referencing the roles listed above
        You can use the "*" symbol to represent access to all roles.

        The left-hand side specifies the name of the access level, and the right-hand side
        specifies what user roles have access to that access level. E.g. users with user role
        'user' and 'admin' have access to the access level 'user'.
         */
        accessLevels : {
            'public' : "*",
            'anon': ['public'],
            'host' : ['host'],
            'renter' : ['renter'],
            'user' : ['host', 'renter', 'admin'],
            'admin': ['admin']
        }

    }

    exports.userRoles = buildRoles(config.roles);
    exports.accessLevels = buildAccessLevels(config.accessLevels, exports.userRoles);

    /*
        Method to build a distinct bit mask for each role
        It starts off with "1" and shifts the bit to the left for each element in the
        roles array parameter
     */

    function buildRoles(roles){

        var bitMask = "01";
        var userRoles = {};

        for(var role in roles){
            var intCode = parseInt(bitMask, 2);
            userRoles[roles[role]] = {
                bitMask: intCode,
                title: roles[role]
            };
            bitMask = (intCode << 1 ).toString(2)
        }

        return userRoles;
    }

    /*
    This method builds access level bit masks based on the accessLevelDeclaration parameter which must
    contain an array for each access level containing the allowed user roles.
     */
    function buildAccessLevels(accessLevelDeclarations, userRoles){

        var accessLevels = {};
        for(var level in accessLevelDeclarations){

            if(typeof accessLevelDeclarations[level] == 'string'){
                if(accessLevelDeclarations[level] == '*'){

                    var resultBitMask = '';

                    for( var role in userRoles){
                        resultBitMask += "1"
                    }
                    //accessLevels[level] = parseInt(resultBitMask, 2);
                    accessLevels[level] = {
                        bitMask: parseInt(resultBitMask, 2)
                    };
                }
                else console.log("Access Control Error: Could not parse '" + accessLevelDeclarations[level] + "' as access definition for level '" + level + "'")

            }
            else {

                var resultBitMask = 0;
                for(var role in accessLevelDeclarations[level]){
                    if(userRoles.hasOwnProperty(accessLevelDeclarations[level][role]))
                        resultBitMask = resultBitMask | userRoles[accessLevelDeclarations[level][role]].bitMask
                    else console.log("Access Control Error: Could not find role '" + accessLevelDeclarations[level][role] + "' in registered roles while building access for '" + level + "'")
                }
                accessLevels[level] = {
                    bitMask: resultBitMask
                };
            }
        }

        return accessLevels;
    }

})(typeof exports === 'undefined' ? this['routingConfig'] = {} : exports);
