        var setMarker = function (position) {
            if (marker) {
                marker.setPosition(position);
            } else {
                marker = new google.maps.Marker({
                    position: position,
                    animation: google.maps.Animation.DROP,
                    //draggable: true,
                    map: map
                });
            }
        };

        $scope.showProfile = function (user) {
            $scope.host = user.role === 'host';
            $scope.admin = user.role === 'admin';
            if (user.role !== 'admin') {
                UserEndPoint.getProfile(user.username)
                    .success(function (data, status, headers, config) {
                        $scope.profile = data;
                        $scope.picture = $scope.profile.picture ? $scope.profile.picture.medium_url : $scope.defaultImg;
                        $scope.confirmed = user.confirmed;
                        setMarker(new google.maps.LatLng(data.location.latitude, data.location.longitude));
                        bounds.extend(marker.getPosition());
                        map.fitBounds(bounds);
                    });
            }
            else {
                $scope.profile = null;
                $scope.picture = "https://fakeimg.pl/350x200/" + Math.random().toString(16).slice(2, 8) + "/000/";
                $scope.confirmed = null;
                removeMarker();
            }
        };

        $scope.deleteUserDialog = function ($event) {
            // Appending dialog to document.body to cover sidenav in docs app
            var confirm = $mdDialog.confirm()
                .title('User Deletion...')
                .textContent('Would you like to delete user ' + $scope.profile.owner + ' ?')
                .targetEvent($event)
                .ok('Yes, do it!')
                .cancel('No, don\'t do it!');
            $mdDialog.show(confirm).then(function () {
                UserEndPoint.deleteUser($scope.profile.owner)
                    .success(function (data, status, headers, config) {
                        alert("The user was successfully deleted.");
                        $state.reload();
                    })
            })
        };

        $scope.confirmUserDialog = function ($event) {
            // Appending dialog to document.body to cover sidenav in docs app
            var confirm = $mdDialog.confirm()
                .title('User Confirmation...')
                .textContent('Would you like to confirm user ' + $scope.profile.owner + ' ?')
                .targetEvent($event)
                .ok('Yes, do it!')
                .cancel('No, don\'t do it!');
            $mdDialog.show(confirm).then(function () {
                UserEndPoint.confirmUser($scope.profile.owner)
                    .success(function (data, status, headers, config) {
                        alert("The user was successfully confirmed.");
                        $state.reload();
                    })
            })
        };

