/**
 * Change top "Daily Downloads", "Active Users" values with animation effect
 */
(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .directive('animatedChange', animatedChange);

  /** @ngInject */
  function animatedChange($timeout) {
    return {
      link: function (scope, element) {
        $timeout(function () {
          var newValue = element.attr('new-value');
          var oldvalue = parseInt(element.html());

          function changeValue(val) {
            $timeout(function () {
              element.html(val);
            }, 30);
          }

          if (newValue > oldvalue) {
            for (var i = oldvalue; i <= newValue; i++) {
              changeValue(i);
            }
          } else {
            for (var j = oldvalue; j >= newValue; j--) {
              changeValue(j);
            }
          }
          $timeout(function () {
            element.next().find('i').addClass('show-arr');
          }, 500);
        }, 3500);
      }
    };
  }

})();
/**
 * Auto expand textarea field
 */
(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .directive('autoExpand', autoExpand);

  /** @ngInject */
  function autoExpand() {
    return {
      restrict: 'A',
      link: function ($scope, elem) {
        elem.bind('keydown', function ($event) {
          var element = $event.target;
          $(element).height(0);
          var height = $(element)[0].scrollHeight;
          height = (height < 16) ? 16 : height;
          $(element).height(height);
        });

        // Expand the textarea as soon as it is added to the DOM
        setTimeout(function () {
          var element = elem;
          $(element).height(0);
          var height = $(element)[0].scrollHeight;
          height = (height < 16) ? 16 : height;
          $(element).height(height);
        }, 0)
      }
    };
  }

})();
(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .directive('autoFocus', autoFocus);

  /** @ngInject */
  function autoFocus($timeout, $parse) {
    return {
      link: function (scope, element, attrs) {
        var model = $parse(attrs.autoFocus);
        scope.$watch(model, function (value) {
          if (value === true) {
            $timeout(function () {
              element[0].focus();
              element[0].select();
            });
          }
        });
        element.bind('blur', function () {
          scope.$apply(model.assign(scope, false));
        });
      }
    };
  }

})();
(function () {
  'use strict';

  /*From:
  * https://codepen.io/thepio/pen/rLNBWr?editors=1010*/
  angular.module('BlurAdmin.theme')
      .directive('emailAvailable', emailAvailable);

  /** @ngInject */
  function emailAvailable($q, $timeout, AuthenticationEndpoint) {
      return {
          restrict: 'AE',
          require: 'ngModel',
          link: function(scope, elm, attr, model) {
              model.$asyncValidators.email_not_available = function() {
                  var defer = $q.defer();
                  AuthenticationEndpoint.checkEmail(model.$viewValue)
                      .success(function (data, status, headers, config) {
                          if (data.email.available) {
                              defer.resolve();
                          }
                          else {
                              scope.checkEmailResponse = "This email is already in use, try another!";
                              defer.reject('This email is already in use, try another!');
                          }
                      }).error(function () {
                      if (status > 0) {
                          scope.checkEmailResponse = data.error.reason.toString();
                          defer.reject(data.error.reason);
                      } else {
                          scope.checkEmailResponse = 'Could not communicate with the remote server, try again later!';
                          defer.reject('Could not communicate with the remote server, try again later!');
                      }
                      defer.reject();
                  });
                  return defer.promise;
              };
          }
      }
  }
})();

