(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .service('baUtil', baUtil);

  /** @ngInject */
  function baUtil() {

    this.isDescendant = function(parent, child) {
      var node = child.parentNode;
      while (node != null) {
        if (node == parent) {
          return true;
        }
        node = node.parentNode;
      }
      return false;
    };

    this.hexToRGB = function(hex, alpha) {
      var r = parseInt( hex.slice(1,3), 16 );
      var g = parseInt( hex.slice(3,5), 16 );
      var b = parseInt( hex.slice(5,7), 16 );
      return 'rgba(' + r + ', ' + g + ', ' + b + ', ' + alpha + ')';
    };

    this.hasAttr = function (elem, attrName) {
      var attr = $(elem).attr(attrName);
      return (typeof attr !== typeof undefined && attr !== false);
    }
  }
})();

(function () {
    'use strict';
    angular.module('BlurAdmin.theme')
        .service('blobToBase64', blobToBase64);

    /** @ngInject */
    function blobToBase64(toastr, $q) {
        return function (file) {
            var deferer = $q.defer();
            var reader = new FileReader();
            reader.onload = function (readerEvt) {
                deferer.resolve(btoa(readerEvt.target.result));
            };
            reader.onerror = function (error) {
                deferer.reject(error);
                toastr.error(JSON.stringify(error), '<strong>File Reader Error!</strong>');
            };
            reader.readAsBinaryString(file);
            return deferer.promise;
        }
    }
})();

(function () {
    'use strict';

    angular.module('BlurAdmin.theme')
        .factory('focus', focus);

    /** @ngInject */
    function focus($rootScope, $timeout, $window) {
        /*From:
        * https://stackoverflow.com/a/25597540/4056565*/
        return function(id) {
            // timeout makes sure that is invoked after any other event has been triggered.
            // e.g. click events that need to run before the focus or
            // inputs elements that are in a disabled state but are enabled when those events
            // are triggered.
            $timeout(function() {
                var element = $window.document.getElementById(id);
                if(element)
                    element.focus();
            });
        };
    }
})();

