(function () {
    'use strict';
    angular.module('BlurAdmin.theme')
        .factory('AuthenticationEndpoint', AuthenticationEndpoint);

    /** @ngInject */
    function AuthenticationEndpoint($http) {
        return {
            authenticateUser: function (credentials) {
                return $http({
                    url: rootUrl + '/authentication/login/',
                    method: "POST",
                    headers: {'Content-Type': 'application/json'},
                    data: credentials
                });
            },
            logout: function () {
                return $http.get(rootUrl + '/authentication/logout/');
            },
            authenticated: function () {
                return $http.get(rootUrl + '/authentication/check/');
            },
            registerUser: function (register) {
                return $http({
                    url: rootUrl + '/authentication/register/',
                    method: "POST",
                    headers: {'Content-Type': 'application/json'},
                    data: register
                });
            },
            checkUsername: function (username) {
                return $http.get(rootUrl + '/authentication/check/username/' + username + '/');
            },
            checkPhone: function (phone) {
                return $http.get(rootUrl + '/authentication/check/phone/' + phone + '/');
            },
            checkEmail: function (email) {
                return $http.get(rootUrl + '/authentication/check/email/' + email + '/');
            }
        }
    }
})();

(function () {
    'use strict';
    angular.module('BlurAdmin.theme')
        .factory('FileEndPoint', FileEndPoint);

    /** @ngInject */
    function FileEndPoint($rootScope, $http) {
        return {
            getImageByIdAndSize: function (picture, size) {
                return $http({
                    cache: true,
                    url: rootUrl + '/files/picture/' + picture + '/',
                    method: "GET",
                    params: {size: size ? size : "picture"}
                });
            }
        }
    }
})();

(function () {
    'use strict';
    angular.module('BlurAdmin.theme')
        .factory('RoomEndPoint', RoomEndPoint);

    /** @ngInject */
    function RoomEndPoint($rootScope, $http) {
        return {
            getRooms: function (params) {
                return $http({url: rootUrl + '/rooms/', method: "GET", params: params});
            },
            getRoom: function (id) {
                return $http({url: rootUrl + '/rooms/' + id + '/', method: "GET"});
            },
            getRoomsByOwner: function (owner) {
                return this.getRooms({owner: owner});
            },
            getRoomPicture: function (id, picture, size) {
                return $http({
                    cache: true,
                    url: rootUrl + '/rooms/' + id + '/picture/' + picture + '/',
                    method: "GET",
                    params: {size: size ? size : "picture"}
                });
            },
            insertRoomPictures: function (id, pictures) {
                return $http({
                    url: rootUrl + '/rooms/' + id + '/picture/',
                    method: "POST",
                    data: pictures
                });
            },
            createRoom: function (room) {
                return $http({
                    url: rootUrl + '/rooms/',
                    method: "POST",
                    data: room
                });
            },
            bookRoom: function (id, username, reservation) {
                return $http({
                    url: rootUrl + '/rooms/' + id + '/book/' + username,
                    method: "POST",
                    data: reservation
                });
            },
            updateRoom: function (owner, room) {
                return $http({
                    url: rootUrl + '/rooms/' + owner,
                    method: "PUT",
                    data: room
                });
            },
            deleteRoom: function (owner) {
                return $http({
                    url: rootUrl + '/rooms/' + owner,
                    method: "DELETE"
                });
            }
        }
    }
})();

(function () {
    'use strict';
    angular.module('BlurAdmin.theme')
        .factory('UserEndPoint', UserEndPoint);

    /** @ngInject */
    function UserEndPoint($http) {
        return {
            getUsers: function () {
                return $http.get(rootUrl + '/users/')
            },
            getUsersByRole: function (role) {
                return $http.get(rootUrl + '/' + role + '/show/')
            },
            getUser: function (username) {
                return $http.get(rootUrl + '/users/' + username)
            },
            getProfile: function (username) {
                return $http.get(rootUrl + '/users/' + username + '/profile/')
            },
            createUser: function (user) {
                return $http({
                    url: rootUrl + '/users/',
                    method: "POST",
                    data: user
                })
            },
            updateUser: function (user) {
                return $http({
                    url: rootUrl + '/users/' + user.username + '/',
                    method: "PUT",
                    data: user
                })
            },
            insertProfilePicture: function (username) {
                return $http({
                    url: rootUrl + '/users/' + username + '/picture',
                    method: "PUT",
                    data: user
                })
            }, //TODO: <---- METAMORFOSI FOTOGRAFIAS ANTI GIA AYTO
            confirmUser: function (username) {
                return $http({
                    url: rootUrl + '/users/' + username + '/confirm/',
                    method: "PUT"
                })
            },
            deleteUser: function (username) {
                return $http({
                    url: rootUrl + '/users/' + username + '/',
                    method: "DELETE"
                })
            }
        }
    }
})();

